import React, {Component} from 'react';
import {Link} from 'react-scroll';
import './arrow.scss'

export default class Arrow extends Component {

    render() {
        return (

            <div className="downArrow bounce">
                <Link
                    to='About'
                    smooth={true}
                    duration={800}
                    isDynamic={true}
                    onClick={this.props.onClick}
                >
                    <img alt="arrow"
                         src="data:image/svg+xml;base64,"/>
                </Link>
            </div>

        );
    }
}
